import {Card, Image, Rating, Text} from '@mantine/core';
import {IconRosetteDiscountCheckFilled, IconSquareRoundedXFilled} from "@tabler/icons-react";
import React, {FC, memo} from 'react';
import {IEmployeeProps} from "./_employee.props";
import styles from './_employee.module.scss';

export const Employee: FC<IEmployeeProps> = memo(({id, name, ratingCount, imgUrl, reviewsCount, isChecked = false, onClick}) => (
    <Card shadow="sm" p="sm" radius="md" withBorder onClick={() => onClick(id)}>
        <Card.Section component="div" className={styles.employee_container}>
            {isChecked && id !== 0 &&(
                <>
                    <IconRosetteDiscountCheckFilled className={styles.employee_checked}/>
                    <IconSquareRoundedXFilled className={styles.employee_checked_delete}/>
                </>
            )}
            {!isChecked && (
                <div className={styles.mask}/>
            )}
            <Image
                src={imgUrl}
                height={130}
                alt={name}
            />
        </Card.Section>
        <Text size="sm" weight={500}>
            {name}
        </Text>
        {ratingCount && (
            <div className={styles.rating}>
                <Rating size="xs" value={ratingCount} fractions={2} readOnly/>
                <Text color="dimmed" weight={500} size="xs">
                    {reviewsCount}
                </Text>
            </div>
        )}
    </Card>
));