import React, {useId, useState} from 'react';
import {Checkbox, Input, Modal, SegmentedControl, Tabs, Button} from "@mantine/core";
import styles from './_houseCleaning.module.scss'
import {IconSquareRoundedPlusFilled} from "@tabler/icons-react";
import {ServicesList} from "../ServicesList";

const CHECKBOX = [
    {
        label: "Кухя",
        value: "1",
        checked: true
    },
    {
        label: "Гостиная",
        value: "2",
        checked: true
    },
    {
        label: "Спальня",
        value: "3",
        checked: true
    },
    {
        label: "Балкон",
        value: "4",
        checked: true
    },
]
export const HouseCleaning = () => {
    const [type, setType] = useState("house");
    const [checkbox, setCheckbox] = useState(CHECKBOX);
    const [tabs, setTabs] = useState(CHECKBOX);
    const [openAddModal, setOpenAddModal] = useState(false);
    const [addValue, setAddValue] = useState('');

    const handleCheckboxClick = (event: any) => {
        const {value, checked} = event.currentTarget;
        const update = checkbox.map((item) => {
            if (item.value === value) {
                return {...item, checked}
            }
            return item
        })

        const updateTabs = update.filter((tab) => tab.checked)

        setCheckbox(update)
        setTabs(updateTabs)
    }

    const handleAddTabs = () => {
        setCheckbox((prev) => {
            const update = [...prev, {
                label: addValue,
                value: String(Math.random()),
                checked: true
            }]
            setTabs(update)

            return update
        })

        setOpenAddModal(false);
    }

    return (
        <div className={styles.house_cleaning_container}>
            <Modal
                opened={openAddModal}
                onClose={() => setOpenAddModal(false)}
                title="Добавить помещение"
            >
                <Input placeholder="Название" value={addValue}
                       onChange={(event) => setAddValue(event.currentTarget.value)}/>
                <Button mt={10} onClick={handleAddTabs}>Добавить</Button>
            </Modal>
            <div className={styles.house_cleaning_header}>
                <SegmentedControl
                    onChange={setType}
                    data={[
                        {label: 'Дома', value: 'house'},
                        {label: 'Квартира', value: 'apartments'},
                        {label: 'Отдельных помещений', value: 'parts'},
                    ]}
                />
                {type === 'parts' && (
                    <div className={styles.house_cleaning_checkbox}>
                        {checkbox.map((type) => (
                            <Checkbox label={type.label} value={type.value} checked={type.checked}
                                      onChange={handleCheckboxClick}/>
                        ))}
                        <IconSquareRoundedPlusFilled size="25" onClick={() => setOpenAddModal(true)}/>
                    </div>
                )}
            </div>
            {type === "house" &&  <ServicesList name="дома"/>}
            {type === "apartments" &&  <ServicesList name="квартиры"/>}
            {type === 'parts' && (
                <Tabs defaultValue="1">
                    <Tabs.List>
                        {tabs.map((tab) => (
                            <Tabs.Tab key={tab.value} value={tab.value}>{tab.label}</Tabs.Tab>
                        ))}
                    </Tabs.List>
                    {tabs.map((tab) => (
                        <Tabs.Panel key={tab.value} value={tab.value} pt="xs">
                            <ServicesList/>
                        </Tabs.Panel>
                    ))}
                </Tabs>
            )}
        </div>
    );
};