import {motion} from "framer-motion";
import React, {FC, useState} from "react";

import {IOrdersProps} from "./_orders.props";
import {Button, HouseCleaning, PageTitle } from "../../components";
import {Scheduling} from "../../components/Scheduling/_Scheduling";

import styles from "./_orders.module.scss";
import { Group, Stepper, Tabs} from "@mantine/core";

import {
    IconCircleCheck,
    IconBuildingWarehouse,
    IconRosetteDiscountCheck,
    IconHome,
    IconCalendarEvent,
    IconArrowLeft,
    IconArrowRight
} from "@tabler/icons-react";


const STEPPER_ICON_SIZE = 18


export const Orders: FC<IOrdersProps> = () => {
    const [active, setActive] = useState(0);

    const nextStep = () => setActive((current) => (current < 3 ? current + 1 : current));
    const prevStep = () => setActive((current) => (current > 0 ? current - 1 : current));

    return (
        <motion.section initial={{opacity: 0}} animate={{opacity: 1}}>
            <div className={styles.orders_container}>
                <div className={styles.orders_header}>
                    <div className={styles.orders_name}>
                        <PageTitle>Заказы</PageTitle>
                    </div>
                </div>
                <Tabs defaultValue="home">
                    <Tabs.List>
                        <Tabs.Tab value="home" icon={<IconHome size={STEPPER_ICON_SIZE}/>}>Уборка дома</Tabs.Tab>
                        <Tabs.Tab value="roof" icon={<IconBuildingWarehouse size={STEPPER_ICON_SIZE}/>}>Очистка
                            крыш</Tabs.Tab>
                    </Tabs.List>

                    <Tabs.Panel value="home" pt={40} h="800">
                        <div style={{height: 700, display: "flex", flexDirection: "column", justifyContent: "space-between"}}>
                            <Stepper
                                color="green"
                                active={active}
                                onStepClick={setActive}
                                completedIcon={<IconCircleCheck size={STEPPER_ICON_SIZE}/>}
                            >
                                <Stepper.Step
                                    icon={<IconHome size={STEPPER_ICON_SIZE}/>}
                                    label="Уборка дома"
                                    description="Выбрать помещение и основные опции"
                                >
                                    <HouseCleaning/>
                                </Stepper.Step>
                                <Stepper.Step
                                    icon={<IconCalendarEvent size={STEPPER_ICON_SIZE}/>}
                                    label="Выбрать дату"
                                    description="Выбрать день и специалиста"
                                >
                                    <Scheduling/>
                                </Stepper.Step>
                                <Stepper.Step
                                    icon={<IconRosetteDiscountCheck size={STEPPER_ICON_SIZE}/>}
                                    label="Отправить заявку"
                                    description="Карточка заказа"
                                />
                            </Stepper>
                            <div className={styles.orders_content}>
                                <Group mt="xl">
                                    <Button onClick={prevStep}><IconArrowLeft size={STEPPER_ICON_SIZE}/></Button>
                                    <Button onClick={nextStep}><IconArrowRight size={STEPPER_ICON_SIZE}/></Button>
                                </Group>
                            </div>
                        </div>
                    </Tabs.Panel>

                    <Tabs.Panel value="roof" pt="xs">
                        Messages tab content
                    </Tabs.Panel>
                </Tabs>
            </div>
        </motion.section>
    );
};
